import React, { useState } from 'react'
import { range } from 'lodash'
import styled from '@emotion/styled'
import { keyframes } from '@emotion/react'

import Container from '../../../components/Container'
import Box from '../../../components/Box'
import Image from '../../../components/Image'
import Heading from '../../../components/Heading'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'

import { ReactComponent as IntroMap } from './intro-map.svg'
import ArrowNext from '../../../components/ArrowNext';

import threeStripe from './three-stripe.svg'
import twoStripe from './two-stripe.svg'
import dotGrid from './dot-grid.svg'
import { responsive } from '../../../components/ThemeProvider/theme'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'

import names from '../names'

const ptr = keyframes`
0%,
40%,
80% {
  transform: translate3d(0,0,0);
}

20%,
60% {
  transform: translate3d(25%,0,0);
}
`

const ptt = keyframes`
0%,
40%,
80% {
  transform: translate3d(0,0,0);
}

20%,
60% {
  transform: translate3d(0,25%,0);
}
`

const PointingRight = styled(Box)`
animation: ${ptr} 2s infinite;
`

const PointingTop = styled(Box)`
animation: ${ptt} 2s infinite;
`

const radius =  [0, 1, 3 , 2]


const TheMap = ({ onClick, ...props }) => {
  return (
    <Box {...props}>
      <Container position="relative" py={responsive('1em', '3em')}>
        <Box.Absolute bottom="0" right="-2em" width="30em">
          <Box mixBlendMode="soft-light">
            <Image src={dotGrid} />
          </Box>
          <Box.FullAbs><Image src={twoStripe} /></Box.FullAbs>
        </Box.Absolute>
        <Flex position="relative" flexDirection={responsive('column', 'row')} px={responsive('1em', 0)}>
          <Box width={responsive('100%', '46%')}>
            <Box.Relative mt={responsive('0', '6em')}>
              <Box.Absolute width="25em" top="-1.5em" left="-8em">
                <Image src={threeStripe} />
              </Box.Absolute>
              <Box.Relative>
                <Heading color="white" fontSize="2.25em" letterSpacing="0.125em" lineHeight="2" textAlign={responsive('center', 'left')}>
                  聚焦四大熱門投資主題<br />
                  帶您掌握投資脈動
                </Heading>
              </Box.Relative>
            </Box.Relative>
            <Flex color="sun" alignItems="center" mt="60%" display={responsive('none', 'flex')}>
              <Text fontSize="1.375em">點擊您最感興趣的主題開始了解</Text>
              <PointingRight ml="0.75em">
                <ArrowNext lineWidth="2em" width="3em" />
              </PointingRight>
            </Flex>
          </Box>
          <Box.Relative width={responsive('100%', '54%')} mt={responsive('2em', 0)} p>
            <IntroMap />
            {range(4).map(id => (
              <Box.Absolute
                key={id}
                left={id % 2 ? '51%' : '7%'}
                top={id < 2 ? '7%' : '49%'}
                width={id % 2 ? "42%" : "43%"}
                height={id < 2 ? "42%" : "44%"}
                bg="white"
                opacity={0}
                transition="opacity 500ms"
                _hover={{
                  opacity: 0.3,
                }}
                borderRadius={(() => {
                  const base = Array(4).fill(0)
                  base[radius[id]] = '100%'
                  return base.join(' ')
                })()}
                role="button"
                className="btn-track"
                data-name={`Map-${names[id]}`}
                onClick={() => {
                  window.gaTrackClick(`Map-${names[id]}`)
                  onClick(id)
                }}
              />
            ))}
          </Box.Relative>
          <Box color="sun" mt="1em" display={responsive('block', 'none')} textAlign="center">
            <PointingTop>
              <ArrowNext lineWidth="1.5em" width="2.5em" transform="rotate(-90deg)" transformOrigin="65% 100%" />
            </PointingTop>
            <Text mt="4em" fontSize="0.875em">點擊您最感興趣的主題開始了解</Text>
          </Box>
        </Flex>
      </Container>
    </Box>
  )
}

export default TheMap
